<template>
  <div>
    <b-modal
      modal
      title="TRACKING"
      v-model="mutableIfModalEquipment"
      size="lg"
      modal-class="modal-primary"
      hide-footer
      body-class="mb-2"
      @hidden="closeModal"
      title-tag="h3"
    >
      <div class="table-responsive">
        <!-- Table -->
        <b-table
          :api-url="'/logistics/inventory/get-tracking-request'"
          ref="refPaymentsGrid"
          class="position-relative"
          :items="myProvider"
          stacked="lg"
          :fields="arrayColumns"
          primary-key="id"
          table-class="text-nowrap table-seccion-head-gray"
          responsive
          show-empty
        >
          <template #table-busy>
            <div class="text-center text-primary my-2">
              <b-spinner class="align-middle mr-1"></b-spinner>
              <strong>Loading ...</strong>
            </div>
          </template>
          <!-- Column CREATED BY -->
          <template #cell(created_at)="data">
            {{ data.item.created_by }}
            <br />
            {{ data.item.created_at | myGlobalDay }}
          </template>
          <!-- Column STATUS -->
          <template #cell(status)="data">
            <p
              :style="statusColor(data.item.status)"
            >
              {{ data.item.status }}
            </p>
          </template>
          <!-- Column COMMENTARY -->
          <template #cell(commentary)="data">
            <div
              style="white-space: normal"
              v-html="data.item.commentary"
            ></div>
          </template>
        </b-table>
      </div>
    </b-modal>
  </div>
</template>

<script>
// Import Data
import fields from "../data/fields.viewtracking.data";
export default {
  props: {
    modalTrackingRequest: {
      type: Boolean,
    },
    requestId: {
      type: [Number, String],
    },
    global: {
      type: Object,
    },
  },
  data: function () {
    return {
      mutableIfModalEquipment: this.modalTrackingRequest,
      id: "",
      arrayColumns: fields,
    };
  },
  methods: {
    statusColor: function (status) {
      return status == "DISAPPROVED"
        ? "color: #FF0000"
        : status == "SEND"
        ? "color: rgb(255 177 0);"
        : status == "APPROVED"
        ? "color: blue;"
        : "color: #00CC00";
    },
    closeModal: function () {
      this.$emit("closeTrackingRequest", false);
    },
    myProvider: async function (ctx) {
      try {
        const data = await amgApi.post(`${ctx.apiUrl}`, {
          requestId: this.requestId,
        });
        const items = data.data;
        return items || [];
      } catch (error) {
        console.error(error);
        return [];
      }
    },
  },
};
</script>